@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';
















































































































































































































































































































































































































































































































































































































@import '~@assets/scss/theme.scss';
.add-page {
  .el-dialog--center {
    width: 300px;
    margin-top: calc(50vh - 151px);
    .el-dialog__body {
      padding: 20px;
      text-align: center;
      .el-icon-warning {
        width: 100%;
        height: 30px;
        font-size: 30px;
        display: inline-block;
        color: #eaa926;
        margin: 0 auto;
        text-align: center;
        padding-bottom: 10px;
      }
    }
  }
  font-size: 14px;
  padding-left: 20px;
  .actionsMenuList-wrap {
    display: flex;
    .actionsMenuList-label {
      font-weight: 700;
      flex-shrink: 0;
      width: 60px;
    }
  }
  &-title {
    margin-bottom: 12px;

    .search {
      float: left;
      margin-bottom: 20px;

      .search-item {
        display: inline-block;
        width: 280px;
      }
    }
    .table-botton {
      float: right;
    }
  }

  .el-col {
    margin-bottom: 10px;
  }

  .list-actions {
    float: right;
    margin: 0px 20px 10px 20px;
  }
  .data-list .el-input {
    width: 100% !important;
  }
  .data-list {
    border: 1px solid #efefef;
    margin-top: 10px;
    min-height: 270px;
  }
  .userType-list {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    overflow: hidden;
    border-bottom: 1px solid #efefef;
    padding-left: 10px;
    padding-right: 10px;
  }
  .userType-list:hover {
    background-color: rgba(212, 233, 245, 0.3);
  }
  .userType-list:last-of-type {
    border-bottom: none;
  }
  .userType-list-left {
    display: flex;
    min-width: 240px;
    cursor: pointer;
    padding: 10px 20px 10px 10px;
  }
  .user-action-list {
    padding: 10px;
    max-height: 100px;
    max-width: 1000px;
    min-width: 850px;
    box-sizing: border-box;
    .box-col {
      width: 100px;
      height: 32px;
      line-height: 32px;
      margin-left: 20px;
      .el-checkbox {
        width: 100px;
        height: 32px;
        line-height: 32px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
  }
  .userType-list-right {
    padding-top: 28px;
  }
  .userType-list-right a {
    font-size: 12px;
    border: 1px solid #ccc;
    padding: 2px 10px;
  }
  .userType-list-img {
    display: inline-block;
    vertical-align: middle;
    width: 60px;
    height: 60px;
    margin-right: 10px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }

    .head-portrait {
      float: left;
      display: block;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background: rgba(#30b3b3, 0.6);
      color: $main-background-color;
      text-align: center;
      line-height: 60px;
      margin-right: 8px;
      font-size: 32px;
    }
  }

  .userType-list-info {
    display: inline-block;
  }
  .userType-list-info a span:nth-of-type(2) {
    margin-left: 10px;
    font-size: 12px;
    background: #eee;
    border-radius: 15px;
    padding: 0 10px;
  }
  .userType-list-info a {
    position: relative;
    top: 10px;
    height: 18px;
    white-space: nowrap;
  }
  .userType-list-info p {
    position: relative;
    top: 15px;
    height: 20px;
  }
  .panelPosition {
    .blueLine {
      top: 30px;
    }
    .el-tab-two {
      display: inline-block;
    }
    .el-tabs__header {
      top: 0px;
    }
    .tabPaneStyle {
      padding: 20px 40px 0px 10px;
    }
    .addInfo {
      margin-top: 20px;
      float: right;
      .form-sub-btn {
        cursor: pointer;
        .icon_save {
          width: 14px;
          height: 14px;
          color: $auxiliary-icon-color;
        }
        &:hover {
          .icon_save {
            color: $button-selected-color;
          }
        }
      }
    }
  }

  .add-page-form {
    .actionsMenuList-label-item {
      display: inline-block;
      line-height: 13px;
      width: 85px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
  .el-form-item__error {
    margin-left: 25%;
  }
}
