@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';






































































































































































































































































































































































































































































































































































































































@import '~@assets/scss/theme.scss';
.menu-manage {
  .el-tree-node__content {
    height: 40px !important;
    .el-tree-node__expand-icon {
      font-size: 14px;
    }
    .icon-wrap {
      float: right;
      display: none;
      .menu-item-icon {
        color: $link-text-color;
        padding-right: 10px;
      }
    }
    &:hover {
      background-color: $color-main-opcity;
      color: $color-main;
      .icon-wrap {
        display: block;
      }
    }
  }
  .el-tree-node:focus > .el-tree-node__content {
    background-color: $color-main-opcity;
    color: $color-main;
  }
}

.user-detail {
  display: flex;
  border-radius: 4px;
  background-color: $color-back-card;
  transition: background-color 0.8s;
  min-height: calc(100vh - 200px);
  padding: 20px 0 20px 20px;
  font-size: 0;
  .menu-list-wrap {
    height: calc(100vh - 130px - 44px);
    vertical-align: top;
    width: 320px;
    flex-grow: 0;
    display: inline-block;
    border-right: 1px solid $table-border-color;
    border-radius: 0;
    padding: 0 20px 0 0;
    font-size: 14px;
    .left-item-header {
      margin-top: -20px;
      margin-bottom: 20px;
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid $table-border-color;
      &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: -2px;
        width: 5px;
        height: 16px;
        background-color: #2a76cd;
      }
      .item-header-icon {
        position: relative;
        padding-left: 14px;
        color: $color-subtitle-text;
        font-size: 16px;
      }
      .icon_init {
        cursor: pointer;
        width: 14px;
        height: 14px;
        color: $button-selected-color;
      }
    }

    .base-info {
      position: relative;
      padding: 10px;
      margin-bottom: 18px;

      .title {
        margin-bottom: 12px;

        .font3 {
          color: $text-color-help;
          font-weight: 500;
          margin-left: 0.8em;
        }
      }
    }

    .role-list {
      font-size: 14px;
      .role-list-title {
        div {
          .showBackground {
            .backgroundChange {
              position: relative;
              width: 36px;
              height: 32px;
              border-radius: 0 4px 4px 0;
              margin-right: -5px;
              .icon {
                position: absolute;
                right: 10px !important;
                top: 9px;
                width: 14px;
                height: 14px;
                color: #cccccc;
              }
            }
          }
        }
      }

      .role-list-tree {
        margin-bottom: 0;
        border: none;
        height: calc(100vh - 130px - 40px - 35px - 20px - 50px);
        margin-top: 20px;
        overflow: auto;
        &::-webkit-scrollbar {
          width: 8px;
        }
        &::-webkit-scrollbar-thumb {
          border-radius: 6px;
          background: #dbdcdc;
        }
        .el-tree-node__content {
          height: inherit;
        }
        .action {
          border: 0.5px solid #dbdcdc;
        }
      }
    }

    .item-warp {
      width: 100%;
      display: flex;
      justify-content: space-between;
      .info-item {
        display: flex;
        align-items: center;
        width: 100%;
        .label {
          display: block;
          line-height: 26px;
          text-align: left;
          padding: 0 5px 0 0;
          box-sizing: border-box;
          width: 100%;
          .menu-title {
            display: inline-block;
            width: 200px;
          }
        }

        .content {
          margin-left: 20px;
          line-height: 32px;
          position: relative;
          max-height: 100px;
          overflow: hidden;
          width: 100%;
          &.expand {
            max-height: none;
          }
          &-item {
            flex: 0 0 auto;
            margin-right: 40px;
            margin-top: 20px;
            border: 1px solid #ccc;
            border-radius: 5px;
            padding: 2px 10px;
          }

          &:before,
          &:after {
            display: table;
            content: '';
          }

          &:after {
            clear: both;
          }

          .box-col {
            width: 140px;
            height: 32px;
            margin-left: 20px;
            .el-checkbox {
              width: 140px;
              height: 32px;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
            }
          }
        }

        &-add {
          cursor: pointer;
          display: inline-block;
          width: 22px;
          margin-left: 10px;
          text-align: center;
          border: 1px solid rgb(55, 149, 211);
          border-radius: 3px;
        }

        &:before,
        &:after {
          display: table;
          content: '';
        }

        &:after {
          clear: both;
        }
      }

      .edit {
        margin: 0 20px;
      }
    }

    .bottomBtn {
      display: inline-block;
      margin-top: 1em;
      margin-bottom: 1em;
      margin-left: 40%;
    }
  }
  .menu-content-wrap {
    padding: 0 20px 0 0;
    vertical-align: top;
    flex-grow: 1;
    display: inline-block;
    font-size: 14px;
    margin-top: -20px;
    height: calc(100vh - 130px - 22px);
    overflow: auto;
    &::-webkit-scrollbar {
      width: 8px;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 6px;
      background: #dbdcdc;
    }
    .panel-title {
      margin-bottom: 20px;
    }
  }
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 8px;
  }
  .panel .panel-body {
    padding: 0;
  }
}
